import React from 'react';
import cls from './style.module.scss';
import ProfileStatus from "./ProfileStatus";

function ProfileInfo(props) {
    return (

        <div className={cls.profile__info}>

            <div className={cls.avatar}
                 style={{backgroundImage: `url(${ props.profile.photos.large != null ? props.profile.photos.large : 'data:image/png;base64,'})`}}/>

            <div className={cls.info}>
                <p className={cls.name}>{props.profile.fullName}</p>
                <ProfileStatus status={props.status}
                               updateStatus={props.updateStatus}/>
                <div className={`line ${cls.line}`}>
                    <p>City:</p>
                    <p>13.02.96</p>
                </div>
                <div className={`line ${cls.line}`}>
                    <p>Education:</p>
                    <p>13.02.96</p>
                </div>
                <div className={`line ${cls.line}`}>
                    <p>Web Site:</p>
                    <p>cola.com</p>
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo;